.logo {
  background: none;
}

.wrapper-main {
  margin: 0 20vw;
}

p {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 16px;
}

h2 {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 32px;
}

#header-main nav {
  grid-template-rows: auto;
  grid-template-columns: 240px 3fr;
  padding-bottom: 60px;
  display: grid;
}

#header-main nav img {
  height: 50px;
  border-radius: 50px;
  margin: 25px 0;
}

#header-main nav ul {
  justify-self: end;
}

#header-main nav ul li {
  justify-self: end;
  margin: 40px 0 40px 30px;
  list-style: none;
  display: inline-block;
}

#header-main nav ul li a {
  color: #111;
  text-transform: uppercase;
  font-family: Arial, Helvetica, sans-serif;
  text-decoration: none;
}

